.projects {
    padding: 40px 10%;
    text-align: center;
  }
  
  .projects h2 {
    font-size: 1.5em;
    margin-bottom: 20px;
  }
  
  .projects-grid {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
  }
  
  .project-card {
    background: #f5f5f5;
    padding: 20px;
    width: calc(50% - 10px);
    border-radius: 8px;
    text-align: left;
  }
  
  .project-card h3 {
    margin-top: 0;
  }
  
  .project-card a {
    color: #007acc;
    text-decoration: none;
  }
  